.around_filtren {
	position: relative;
}
.around_filtren .filterfixed.fixed {
	position: fixed;
	top: 0px;
	width: 251px;
  z-index: 200;
}
.around_filtren .filterfixed.stop{
	position:relative;
}

.filtren{
	position: relative;
	background-color: #fff;
	border-top: 1px solid #F3F3F3;
	border-bottom: 1px solid #F3F3F3;
	border-left: 1px solid #F3F3F3;
}
.filtren .smartf-div {
	display: block;
}
.filtren .title,
.filtren .filtercompare {
	display: none;
	margin-bottom: 5px;
}
.filtren .title a:hover {
	text-decoration:none;
}
.filtren .title a span{
	border-bottom:1px dashed #214496;
}
.filtren.opened .title span.show,
.filtren .title span.hide{
	display:none;
}
.filtren .title span.show,
.filtren.opened .title span.hide{
	display:inline;
}
.filtren .filtercompare{
	float:right;
}
.filtren .showchild{
	display:block;
	padding:7px 20px 7px 7px;
	color:#666666;
	text-transform:uppercase;
	background-color:#F3F3F3;
}
.filtren .showchild:hover{
	color: $btn1-text-color-hover;
	text-decoration: none;
	background-color: $btn1-bg-color-hover;
}
.filtren .showchild .svg-icon {
	width: 8px;
	height: 8px;
	margin-bottom: 2px;
	margin-right: 5px;
	fill: #000;
	transition: transform .2s;
}
.filtren .showchild:hover .svg-icon {
	fill: #fff;
}
.filtren .closed .showchild .svg-icon {
	transform: rotate(-90deg);
}
.filtren .showchild .hint {
	position: relative;
	display: inline-block;
	margin-left: 10px;
	padding: 0 6px;
	border: 1px solid #ccc;
}
.filtren .showchild:hover .hint {
	border-color: #fff;
}
.filtren .showchild .hint span {
	position: absolute;
	top: 25px;
	left: 0px;
	display: none;
	width: 200px;
	padding: 5px 8px;
	color: #484848;
	font-size: 12px;
	text-transform: none;
	background-color: rgba(243,243,243,0.8);
	border: 1px solid #ccc;
	z-index: 101;
}
.filtren .showchild .hint:hover span {
	display: block;
}

.filtren .lvl1.closed > div {
	display:none;
}
.filtren .lvl1 .property {
	padding:8px 4px;
}
/* CHECKBOXES_WITH_PICTURES & CHECKBOXES_WITH_PICTURES_AND_LABELS */
.filtren .cwp .lvl2,
.filtren .cwp .lvl2 .smartf-div,
.filtren .lvl2 .smartf-div label,
.filtren .lvl2 .smartf-div label span,
.filtren .dd .lvl2 .smartf-div label {
	display: inline-block;
}
.filtren .cwp .lvl2 .smartf-div label,
.filtren .cwpal .lvl2 .smartf-div label,
.filtren .dd .lvl2 .smartf-div label {
	cursor: pointer;
	padding: 5px 7px;
}
.filtren .lvl1 .property .smartf-div.disabled label {
	cursor: default;
}
.filtren .lvl1 .property > div:before {
	content: ' ';
}
.filtren .lvl2 .smartf-div.disabled label.pic span {
	position: relative;
}
.filtren.cross .lvl2 .smartf-div.disabled label.pic span:before {
	content: '';
	position: absolute;
	display: block;
	width: auto;
	height: auto;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background: rgba(0,0,0,0) url('img/cross.svg') no-repeat;
	background-size: 100% 100%;
}
.filtren.opacity .lvl2 .smartf-div.disabled label.pic span {
	filter: progid:DXImageTransform.Microsoft.Alpha(opacity=20);
	-moz-opacity: .2;
	-khtml-opacity: .2;
	opacity: .2;
}
.filtren.hide .cwp .lvl2 .smartf-div.disabled,
.filtren.hide .cwpal .lvl2 .smartf-div.disabled,
.filtren.hide .wpal .lvl2 .smartf-div.disabled {
	display: none;
}
.filtren .lvl2 .smartf-div label.pic span.nopic {
	background-image: url('img/all.jpg');
}
.filtren .lvl2 .smartf-div label.pic:hover {
	background-color:#2d2d2d;
}
.filtren .lvl2 .smartf-div.disabled label.pic:hover {
	background:none;
}
.filtren .lvl2 .smartf-div label.pic span {
	width:21px;
	height:21px;
	font-size:0;
	vertical-align:middle;
	border:1px solid #EEE;
	background-size: cover;
}
.filtren .lvl2 .active label.pic span {
	border:1px solid #F00;
}
/* CHECKBOXES_WITH_PICTURES_AND_LABELS */
.filtren .cwpal .lvl2 .smartf-div label.pic,
.filtren .dd .lvl2 .smartf-div label.pic {
	float:left;
	margin-right:5px;
}
.filtren .cwpal .lvl2 .smartf-div label.name,
.filtren .dd .lvl2 .smartf-div label.name,
.filtren .dd.wpal .lvl2.selected .smartf-div label.name {
	display: block;
	padding: 5px 14px;
}
.filtren .dd .lvl2:not(.selected) .smartf-div.active label {
	color: #fff;
	background-color: $btn1-bg-color;
}
.filtren .lvl2.selected .smartf-div label.name {
	padding: 0px;
}
/* DROPDOWN */
.filtren .dd .dropdown {
	position: relative;
}
.filtren .dd .selected label .svg-icon {
	width: 8px;
	height: 8px;
	margin-left: 6px;
	margin-bottom: 2px;
}
.filtren .dd .selected label {
	cursor: pointer;
}
.filtren .dd .dropdown > .dropdown {
	position: absolute;
	display: none;
	max-width: 250px;
	left: -7px;
	top: 20px;
	padding: 0px 10px 0px 0px;
	background-color: #FFF;
	border: 1px solid #F2F2F2;
	z-index: 101;
}
.filtren .dd.open .dropdown {
	display: block;
}
.filtren .dd .dropdown label{
	display: block;
	padding: 5px 14px;
	cursor: pointer;
}
.filtren .dd.wpal .dropdown label.pic {
	padding: 5px 7px;
}
.filtren .dd.wpal .dropdown label.name {
	white-space: nowrap;
	margin-left: 37px;
	padding: 7px 7px;
}
.filtren .dd.wpal .dropdown {
	left: -1px;
	top: 18px;
}
.filtren .dd .lvl2:not(.selected) label:hover,
.filtren .dd .lvl2:not(.selected) .active label:hover {
	color: #fff;
	background-color: $btn1-bg-color-hover;
}
.filtren .dd .lvl2:not(.selected) label:hover, 
.filtren .dd .lvl2:not(.selected) label:hover span {
	text-decoration: none;
}
.filtren .c .inputs {
	text-align: center;
}
.filtren .c .calendar {
	width: 72px;
	margin-left: 5px;
	padding: 0px;
	text-align: center;
	border: 1px solid #F2F2F2;
	box-shadow: none;
}
.filtren .c .calendar::-webkit-input-placeholder {color:#000;font-size:13px;}
.filtren .c .calendar::-moz-placeholder          {color:#000;font-size:13px;}/* Firefox 19+ */
.filtren .c .calendar:-moz-placeholder           {color:#000;font-size:13px;}/* Firefox 18- */
.filtren .c .calendar:-ms-input-placeholder      {color:#000;font-size:13px;}
.filtren .c .calendar-icon {
	position: relative;
	top: -1px;
}

.filtren .lvl2 label {
	color: #214496;
}
.filtren .lvl2 .disabled label {
	color: #C9C9C9;
	cursor: default;
}
.filtren .lvl2 label:hover,
.filtren .lvl2 label:hover span {
	text-decoration: underline;
}
.filtren .lvl2 .disabled label.name:hover span,
.filtren .lvl2 .disabled label:hover {
	background-color: transition;
}
.filtren .pricegroup .lvl2 label {
	width:50%;
	float:left;
}
.filtren .number .inputs{
	text-align:center;
}
.filtren .number .min,
.filtren .number .max{
	margin-left:5px;
	padding:0px;
	text-align:center;
	border:1px solid #F2F2F2;
	box-shadow:none;
}

.filtren .property .f_search{
	width: 100%;
	box-shadow: none;
  margin-bottom: 5px;
	padding: 4px 4px 3px 4px;
	border: 1px solid #F2F2F2;
}

.filtren .buttons{
	padding:10px 0px;
	text-align:center;
}
.filtren .separator{
	padding:0px 8px;
}

.filtren .modef{
	left:243px;
	min-width:300px;
	position:absolute;
	top:-5px;
	white-space:nowrap;
	z-index:210;
}
.filtren .modef .arrow{
	padding:6px 7px 6px 0px;
	background-image:url('img/modef_bg.png');
	background-repeat:no-repeat;
	background-position:0px 0px;
}
.filtren .modef .data{
	padding:6px 10px;
	color:#fff;
	background-color:#899cc7;
}
.filtren .modef .data a{
	color:#fff;
	text-decoration:underline;
}
.filtren .modef .data a:hover{
	text-decoration: none;
}

.filtren .f_jscrollpane{
	width:100%;
	height:190px;
	overflow:auto;
}

@media screen and (max-width:875px) {
	body.adaptive .filtren .showchild .note {
		display: none;
	}
}
@media screen and (max-width:605px) {
	body.adaptive {
		.filtren {
			border: none;

			&.opened {
				.body {
					display: block;
				}
			}

			.title,
			.filtercompare {
				display: block;
			}

			.body {
				display: none;
				margin-top: 5px;
			}

			div {
				&.lvl1 {
					width: 100%;
				}
			}
		}
	}
}
